import React, { useState } from 'react'
import { courses } from "../../Dummydata"
import { FaBeer } from 'react-icons/fa';


const CourceSlider = () => {
    const [courceCount, setCourceCount] = useState(0)
    let showCources = 3
    let courceCopy = [...courses]
    const CourceList = courceCopy.map((el, index) => (
        <div key={index} className="cource-slider__cources--cource">
            <img className="cource-slider__cource--img" src={el.img} alt={el.name} />
            <div className="cource-slider__cources--bottom">
                <div className="cource-slider__cources--bottom--title">{el.title}</div>
                <div className="cource-slider__cources--bottom--author">by {el.author}</div>

                <div className="cource-slider__cources--bottom--price">Rs {el.price}</div>


            </div>

        </div>
    ))

    let activeList = []
    showCources === 3 ? (activeList = [CourceList[courceCount], CourceList[courceCount + 1], CourceList[courceCount + 2]]) : (activeList = [CourceList[courceCount], CourceList[courceCount + 1]])


    return (
        <div className="cource-slider">
            <div className="cource-slider__title">Top Cources
                </div>
            <div className="cource-slider__underline"></div>
            <div className="cource-slider__container">
                <div className="cource-slider__back">Back</div>
                <div className="cource-slider__cources">{activeList}</div>

                <div className="cource-slider__forward">Forward</div>


            </div>


        </div>
    )
}

export default CourceSlider